
.player {
  width: 100%;
  height: 42px;
  position: fixed;
  bottom:0;
  padding: 5px 0;
  background-color: #f7f7fa;
  color: #888;
}
.player:before{
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  border-top: 1px solid #979797;
  color: #979797;
  transform-origin: 0 0;
  transform: scaleY(.5);
}
.mp3Pic{
  display: inline-block;
  height: 42px;
  width: 42px;
  float: left;
  margin-left: 5px;
}
.mp3Pic img{
  height: 100%;
  width: 100%;
}
.mp3Textbox{
  margin-left: 5px;
  float:left;
  text-align: left;
}
.mp3Textbox p{
  margin: 4px 0;
}
.mp3Name{
  font-size: 14px;
  font-weight: 700;
}
.mp3Author{
  font-size: 12px;
}
.btn-group{
  margin-right: 5px;
  float: right;
  margin-top: 8px;
}
.btn-group i{
  margin: 0 10px;
  font-size: 22px;
}
.btn-group i.icon-caidan{
  color: #4FD6F5;
}
.btn-group i.icon-zanting1{
  color: #FF6868;
}
.btn-group i.icon-bofang{
  color: #09bb07;
}
.btn-group i.icon-xiayishou{
  color: #FFD400;
}



