
.search-box{
  width:100%;
  position: fixed;
  top:0;
}
.search-bar{
  position: relative;
  padding: 8px 10px;
  display: -ms-flexbox;
  display: flex;
  box-sizing: border-box;
  background: #efeff4;
}
.search-outer{
  position: relative;
  -ms-flex: auto;
  flex: auto;
  background-color: #efeff4;
}
.search-outer:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 200%;
    height: 200%;
    transform: scale(.5);
    transform-origin: 0 0;
    border-radius: 10px;
    border: 1px solid #e6e6ea;
    box-sizing: border-box;
    background: #fff;
}
.search-cancel{
  display: block;
  margin-left: 10px;
  line-height: 28px;
  white-space: nowrap;
  color: #09bb07;
}
.search-inner {
    position: relative;
    padding-left: 30px;
    padding-right: 30px;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    z-index: 1;
}
.search-inner .icon-sousuo_sousuo {
    position: absolute;
    left: 10px;
    top: 2px;
    line-height: 28px;
}
.icon-sousuo_sousuo {
    color: #b2b2b2;
    font-size: 14px;
}
.search-inner .search-input {
    padding: 4px 0;
    width: 100%;
    height: 1.42857143em;
    border: 0;
    font-size: 14px;
    line-height: 1.42857143em;
    box-sizing: content-box;
    background: transparent;
}
.cells {
  margin-top: 44px;
  background-color: #fff;
  line-height: 1.41176471;
  font-size: 14px;
  overflow: hidden;
  position: relative;
}
.cell {
    padding: 10px 15px;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    text-align: left;
}
.cells:after, .cells:before {
    content: " ";
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    color: #d9d9d9;
    transform: scaleY(.5);
}
.cell:before {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 1px;
    border-top: 1px solid #d9d9d9;
    color: #d9d9d9;
    transform-origin: 0 0;
    transform: scaleY(.5);
    left: 15px;
}
.cell:active{
  background-color: #ececec;
}
.cells:before {
    top: 0;
    border-top: 1px solid #d9d9d9;
    transform-origin: 0 0;
}
.cells:after {
    bottom: 0;
    border-bottom: 1px solid #d9d9d9;
    transform-origin: 0 100%;
}
.cell-primary {
    -ms-flex: 1;
    flex: 1;
}
.cell-primary p {
    text-align: left;
}
.cell-primary span {
    font-size: 12px;
    color: #666;
}
.cell-btn i{
  font-size: 26px;
  color: #09bb07;
}



